



.accounts_header{
	margin: 0 auto;
	padding: 3%;
	max-width: 90%;
}

.addBtn{
	cursor: pointer;
	margin-left: 12px;
	border-radius: 13px;
	padding: 6px 9px;
	font-size: 16px;
	line-height: 18px;
	background-color: transparent;
	will-change: filter;
}

.addBtn:hover{
	color: #F3F3F3;
	background-color: #3d3d3d;
	--add-btn_hover : background-color  250ms ease-in, color  250ms ease-in;
	transition: var(--add-btn_hover);
	filter: drop-shadow(0 0 2em #3d3d3d);
}

.purple.addBtn{
	color: #646cff;
}

.purple.addBtn:hover{
	color: #F3F3F3;
	background-color: #646cff;
	filter: drop-shadow(0 0 2em #646cff);
}


.form_title {
	color: #646cff;
	margin: 15px 7px 5px;
	font-weight: 600;
	font-size: 19px;
  }

.account_item_box{
	padding: 16px;
	margin: 2px;
}

.account_item_box:hover {
	background-color: #f1f1f1;
	cursor: pointer;
	border-radius: 7px;
}


.account_item_box .code{
	line-height: 21px;
	margin-right: 11px;
}

.account_item_box .name{
	line-height: 21px;
	
}

.account_item_box:hover .code{
	color: #646cff;
	font-weight: 600;
	font-size: 19px;
}

.account_item_box:hover .name{
	color: #646cff;
}

.accounts_header h2{
	margin: 7px 0 15px;
	text-align: left;
}






.pickBx{
	/*filter: drop-shadow(0 0 2em #f3f3f3);*/
	border-radius: 16px;
	min-height: 450px;
	min-width: 560px;
	background-color: rgba(255, 255, 255, 0.97);

}


.pickBx .header{
	padding: 7px 16px;
	margin: 0 16px;
}

.pickBx .header .bottom_loading{
	background-color: #afafaf;
	height: 1px;
	margin: 5px 16px 0;
}


.pickBx .header h3{
	margin: 0 3px;
}


@keyframes loadcenter {
	0% {
		width: 0;
	}
	50% {
		width: 50%;
	}
	100% {
		width: 100%;
	}
}


.pickBx .header
.bottom_loading.animate{
	height: 2px;
	margin: 4px auto 0;
	background-color: #646cff;
	animation: loadcenter .65s infinite alternate ease-in-out;
}





.account_item_pck{
